import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ClientContainerComponent } from './client-container/client-container.component';
import { ClientDashboardComponent } from './client-dashboard/client-dashboard.component';
import { ClientScheduledReportsComponent } from './client-scheduled-reports/client-scheduled-reports.component';
import { AdminGridContainerComponent } from '../admin-grid-container/admin-grid-container.component';
import {
  clientAlertsGuard,
  clientAssetsGuard,
  clientGeofencesGuard,
} from '../../guards';
import { createGuardedGridRoute } from '../../../../shared/utils';
import { AdminTreetableContainerComponent } from '../admin-treetable-container/admin-treetable-container.component';

const routes: Routes = [
  {
    path: '',
    component: ClientContainerComponent,
    children: [
      {
        path: 'accounts',
        children: [
          {
            path: '',
            redirectTo: 'client-users',
            pathMatch: 'full',
          },
          {
            path: ':type',
            component: AdminGridContainerComponent,
          },
          {
            path: ':type/:entityType/:entityId',
            component: AdminGridContainerComponent,
          },
        ],
      },
      {
        path: 'dashboard',
        component: ClientDashboardComponent,
      },
      {
        path: 'details',
        loadChildren: () =>
          import('./client-details/client-details-routing.module').then(
            (m) => m.ClientDetailsRoutingModule,
          ),
        data: { nestedContentScrollable: true },
      },
      {
        path: 'hardware',
        children: [
          {
            path: '',
            redirectTo: 'client-hardware-devices',
            pathMatch: 'full',
          },
          {
            path: ':type',
            component: AdminGridContainerComponent,
          },
          {
            path: ':type/:entityType/:entityId',
            component: AdminGridContainerComponent,
          },
        ],
      },
      {
        path: 'organization',
        children: [
          {
            path: '',
            redirectTo: 'client-cost-centres',
            pathMatch: 'full',
          },
          {
            path: ':type',
            component: AdminTreetableContainerComponent,
          },
          {
            path: ':type/:entityType/:entityId',
            component: AdminTreetableContainerComponent,
          },
        ],
      },
      {
        path: 'profiles',
        children: [
          {
            path: '',
            redirectTo: 'client-io-types',
            pathMatch: 'full',
          },
          {
            path: ':type',
            component: AdminGridContainerComponent,
          },
          {
            path: ':type/:entityType/:entityId',
            component: AdminGridContainerComponent,
          },
        ],
      },
      {
        path: 'providers',
        children: [
          {
            path: '',
            redirectTo: 'client-sms-gateway-providers',
            pathMatch: 'full',
          },
          {
            path: ':type',
            component: AdminGridContainerComponent,
          },
          {
            path: ':type/:entityType/:entityId',
            component: AdminGridContainerComponent,
          },
        ],
      },
      {
        path: 'scheduled-reports',
        component: ClientScheduledReportsComponent,
      },
      ...createGuardedGridRoute(
        'assets',
        clientAssetsGuard,
        AdminGridContainerComponent,
      ),
      ...createGuardedGridRoute(
        'geofences',
        clientGeofencesGuard,
        AdminGridContainerComponent,
      ),
      ...createGuardedGridRoute(
        'alerts',
        clientAlertsGuard,
        AdminGridContainerComponent,
      ),
      {
        path: '',
        redirectTo: 'details',
        pathMatch: 'full',
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ClientsRoutingModule {}
